@mixin  flex 
{
    display: flex;
    justify-content: center;
    align-items: center;
}
.profileChooseCon
{
    @include flex();
    position: absolute;
    width:100%;
    height: 100%;
    background: red;
    z-index: 10;
    background-color: rgba(0, 0, 0, 0.342);
    animation: fadeInProfileImg 0.5s forwards;
    @keyframes fadeInProfileImg 
    {
        0%{opacity: 0;}
        100%{opacity: 1;}
    }
    .profileChoose
    {
        @include flex();
        position: relative;
        flex-wrap: wrap;
        width: 95%;
        min-height: 400px;
        background: white;
        border-radius: 10px;
        padding: 10px;
        padding-top: 25px;
        padding-bottom: 25px;
        box-shadow: 0px 0px 15px rgba(187, 187, 187, 0.452), 
        0 10px 5px rgba(0,0,0,0);
        button
        {
            @include flex();
            width: 30px;
            height: 30px;
            border: none;
            background: none;
            outline: none;
            position: absolute;
            cursor: pointer;
            top: 10px;
            right: 10px;
            div
            {
                position: absolute;
                width: 30px;
                height: 6px;
                border-radius: 25px;
                background: #bbbbbb;
                transform: rotate(45deg);
            }
            div:nth-child(2)
            {
                transform: rotate(-45deg);
            }
        }
        .profileChooseImg
        {
            @include flex();
            width: 100px;
            height: 100px;
            border-radius: 50%;
            margin: 10px;
            position: relative;
            overflow: hidden;
            svg
            {
                width: 120px !important;
                height: 120px !important;
                position: absolute !important;
                pointer-events: none;
            }
            .overlay
            {
                @include flex();
                width: 100%;
                height: 100%;
                background-color: rgba(255,255,255,0.5);
                z-index: 1;
                border: 3px solid #1e92fd;
                border-radius: 50%;
                pointer-events: none;
                div
                {
                    display: inline-block;
                    transform: rotate(45deg);
                    height: 40px;
                    width: 20px;
                    margin-left: 9%;
                    border-bottom: 7px solid #1e92fd ;
                    border-right: 7px solid#1e92fd ;
                    pointer-events: none;
                }
            }
        }
    }
}
.fadeOutProfileImg
{
    animation: fadeOutProfileImg 0.3s forwards;
    @keyframes fadeOutProfileImg 
    {
        0%{opacity: 1;}
        100%{opacity: 0;}
    }
}